.container {
  width: 1240px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.search-box {
  background: #3f57b3;
  padding: 15px 0;
}

.search-form {
  text-align: center;
  margin: 0 auto;
  padding: 0 50px;
}

.search-input {
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 5px 10px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.search-btn {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
}

.photo-card {
  width: calc(25% - 7.5px);
}

.gallery {
  width: 1220px;
  flex-wrap: wrap;
  gap: 10px;
  display: flex;
}

.photo-card-img {
  width: 100%;
  height: 220px;
}

.info {
  text-align: center;
  border: none;
  border-radius: 4px;
  justify-content: space-between;
  font-family: Roboto, sans-serif;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.load-more {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #283870;
  border: none;
  border-radius: 4px;
  margin-top: 10px;
  padding: 5px 10px;
  font-family: Arial, sans-serif;
}

.alignment {
  text-align: center;
}

.info-item {
  font-size: 16px;
  font-weight: bold;
}

.span {
  padding-top: 20px;
  font-size: 14px;
  font-weight: normal;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

